body {
    margin: 0;
    padding: 0;
    position: relative;
    box-sizing: border-box;
}

button {
    background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.navbar .logo .nav-txt {
    color: rgb(180, 180, 180);
    float: left;
    text-align: center;
    display: block;
    padding: 15px 15px;
    font-size: 20px;
    font-weight: 900;
    margin-left: 1vw;
    margin-right: 3vw;
}

.navbar {
    z-index: 99;
    overflow: hidden;
    background-color: #232323;
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    box-shadow: 0px 0px 10px;
    display: flex;
    justify-content: space-between;
}

.navigate {
    display: flex;
    align-items: center;
}

.nav-btn {
    margin-right: 15px;
    float: left;
    text-align: center;
    display: block;
    padding: 5px 10px 5px 10px;
    text-decoration: none;
}

.navbar .nav-txt {
    font-family: 'Quicksand';
    text-decoration: none;
    color: white;
}
.navbar .nav-btn:hover {
    border-radius: 10px;
    box-shadow: 0px 0px 2px black;
    background-color: rgb(141, 106, 106);
    transition: 0.2s;
    cursor: pointer;
}

.nav-txt:hover {
    cursor: pointer;
}

.icons {
    display: flex;
    align-items: center;
    padding-right: 50px;
}

.icon {
    width: 25px;
    height: 25px;
    padding-top: 18.9px;
    padding-bottom: 18.9px;
    padding-left: 30px;
    -webkit-filter: invert(100%);
            filter: invert(100%);
}

.icon:hover {
    -webkit-filter: invert(60%);
            filter: invert(60%);
    transition: 0.4s;
}

