p {
    margin: 0;
}

.frontpage {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #1C1C1C;
    height: 95vh;
    margin-top: 0;
    background-attachment: fixed;
    z-index: -99;
    flex-direction: column;
}

.frontpage-content {
    padding: 50px;
    position: absolute;
    display: flex;
    align-items: center;
    -webkit-animation: shrink 1s 3s forwards;
            animation: shrink 1s 3s forwards;
}

@-webkit-keyframes shrink {
    from {-webkit-transform: scale(1) translateY(0);transform: scale(1) translateY(0);}
    to {-webkit-transform: scale(0.7) translateY(-330px);transform: scale(0.7) translateY(-330px);}
}

@keyframes shrink {
    from {-webkit-transform: scale(1) translateY(0);transform: scale(1) translateY(0);}
    to {-webkit-transform: scale(0.7) translateY(-330px);transform: scale(0.7) translateY(-330px);}
}

.opening-text p {
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 35px;
    line-height: 45px;
    opacity: 0;
    -webkit-animation: appear 1s forwards;
            animation: appear 1s forwards;
    -webkit-animation-delay: 1.8s;
            animation-delay: 1.8s;
}

.photo {
    width: 200px;
    height: 200px;
    margin-right: 30px;
}

.opening-image {
    box-shadow: 0px 4px 4px rgba(110, 110, 110, 0.25);
    border-radius: 15px;
    width: 100%;
    height: 100%;
    opacity: 0;
    -webkit-animation: appear 1s forwards;
            animation: appear 1s forwards;
    -webkit-animation-delay: 1.8s;
            animation-delay: 1.8s;
}

@-webkit-keyframes appear {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes appear {
    from {opacity: 0;}
    to {opacity: 1;}
}

.look-bar {
    border-radius: 5px;
    bottom: 65px;
    position: absolute;
    content: "";
    height: 30px;
    background-color: rgb(122, 112, 112);
    -webkit-animation: bar-movement 3s forwards ease;
            animation: bar-movement 3s forwards ease;
    z-index: -1;
}

@-webkit-keyframes bar-movement {
    0% {left:95%; width: 5%; opacity: 0;}
    30% {left:95%; width: 5%; opacity: 1;}
    60% {left:0; width: 5%; opacity: 1;}
    100% {left:0; width: 100%; opacity: 1;}
}

@keyframes bar-movement {
    0% {left:95%; width: 5%; opacity: 0;}
    30% {left:95%; width: 5%; opacity: 1;}
    60% {left:0; width: 5%; opacity: 1;}
    100% {left:0; width: 100%; opacity: 1;}
}

.logos {
    display: flex;
    opacity: 0;
    -webkit-animation: appear 1s forwards;
            animation: appear 1s forwards;
    -webkit-animation-delay: 4s;
            animation-delay: 4s;
    padding-right: 50px;
    padding-top: 80px;
}

