p {
    margin: 0;
}

br {
    display: block;
    content: "";
    margin: 20px 0;
}

.arrows {
    display: block;
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    width: 60px;
	height: 72px;
    top: 10px;
    transition: 1s all ease;
}

.arrows path     {
    opacity: 0;
	stroke: #8a7d63;
	fill: transparent;
	stroke-width: 1px;
}

.a1 {
    -webkit-animation: appear 1s 4s, arrow 2s 3s infinite;
            animation: appear 1s 4s, arrow 2s 3s infinite;
}

.a2 {
    -webkit-animation: appear 1s 4s, arrow 2s 3.5s infinite;
            animation: appear 1s 4s, arrow 2s 3.5s infinite;
}

.a3 {	
    -webkit-animation: appear 1s 4s, arrow 2s 4s infinite;	
            animation: appear 1s 4s, arrow 2s 4s infinite;
}

@-webkit-keyframes arrow
{
0% {opacity:0}
40% {opacity:1}
80% {opacity:0}
100% {opacity:0}
}

@keyframes arrow
{
0% {opacity:0}
40% {opacity:1}
80% {opacity:0}
100% {opacity:0}
}

